import React from "react";
import MainNav from "./mainpage.nav.component";
import axios from "axios";
import Cookie from 'js-cookie';

const Login = (props) => {

    const user = {
        email: '',
        password: ''
    };

    const changeHandler=(e)=>{
        const name = e.target.name;
        user[name] = e.target.value;
    }

    const submitHandler = (e) => {
        e.preventDefault();
        axios.post('http://localhost:3000/login', {user})
        .then( (res) => {
            if(res.headers.authtoken){
                const userId = res.data.userID;
                //console.log(res.headers.authtoken);
                setTimeout(()=>{window.location.href = 'http://localhost:3000/Account/id='+ userId}, 1000);
                Cookie.set('userInfo', JSON.stringify({user}));
            }
        });
    };

        return (
            <div className='App'>
                <MainNav/>
                <div className="auth-wrapper">
                        <div className="auth-inner">
                            <form>
                                <h3>Sign In</h3>
                                <div className="form-group">
                                    <label>Email address</label>
                                    <input type="email" className="form-control" name="email" placeholder="Enter email" onChange={changeHandler}/>
                                </div>

                                <div className="form-group">
                                    <label>Password</label>
                                    <input type="password" className="form-control" name="password" placeholder="Enter password" onChange={changeHandler}/>
                                </div>

                                <div className="form-group">
                                    <div className="custom-control custom-checkbox">
                                        <input type="checkbox" className="custom-control-input" id="customCheck1" />
                                        <label className="custom-control-label" htmlFor="customCheck1">Remember me</label>
                                    </div>
                                </div>

                                <button type="text" className="btn btn-primary btn-block" onClick={submitHandler}>
                                    Log In
                                </button>

                                <p className="forgot-password text-right">
                                    Forgot <a href="#">password?</a>
                                </p>
                            </form>
                        </div>
                    </div>
            </div>
        );

}

export default Login;