import React from "react";
import axios from "axios";

const updateEmail = (props) => {

    let email = '';
    const id = props.id;

    const submitHandler = (e) => {
        e.preventDefault();
        axios.put('http://localhost:3000/updateEmail', {email:email, id: id})
        .then( (res) => {
            console.log('Update worked!');
            document.getElementById("updateEmail").reset();
        });
    };

    return (
        <div className="auth-wrapper">
            <div className="auth-inner">
                <form id="updateEmail">
                    <h3>Update Email</h3>
                    <div className="form-group">
                        <label>New Email address</label>
                        <input type="email" className="form-control" name="email" placeholder="Enter new email" onChange={(e) => {email = e.target.value}}/>
                    </div>
                    <button type="text" className="btn btn-primary btn-block" onClick={submitHandler}>Submit</button>
                </form>
            </div>
        </div>
    );
}

export default updateEmail;