import React, {useState} from 'react'
import axios from 'axios'
import AccountNav from "./account.nav.component";
import AccountInfo from "./accountInfo.component"
import Order from "./order.component";  ///1
import UpdateEmail from "./updateEmail.component";   ///2
import UpdatePassword from "./updatePassword.component";   ///3
import Search from "./search.component"; //5
import ManageUsers from "./manage.users.component";
//import Display from "./display.component";

const Account = (props) => {

    const id = props.location.pathname.substring(12);
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [type, setType] = useState('')
    const [page, setPage] = useState(0);
    const [orders, setOrders] = useState([]);
    let toRender;
    //let orders = [];

    axios.get('http://localhost:3000/user/' + id).then( (res) => {
        //console.log(res.data.userName);
        setName(res.data.userName);
        setEmail(res.data.userEmail);
        if (res.data.userType === 1){
            setType("Customer");
        }else if(res.data.userType === 2){
            setType("Delivery Person");
        }else if(res.data.userType === 3){
            setType("Admin");
        }
    });

    const changePage = (newPage) => {
        setPage(newPage);
    }

    const addOrder = (order) => {
        setOrders(orders.concat(order));
    }

    switch(page){
        case 0:
            toRender = <AccountInfo name={name} email={email} type={type}/>
            break;
        case 1:
            toRender = <Order Orders={orders}/>;
            break;
        case 2:
            toRender = <UpdateEmail id={id}/>;
            break;
        case 3:
            toRender = <UpdatePassword id={id}/>;
            break;
        case 4:
            toRender = <Search addOrder={addOrder}/>
            break;
        case 6:
            toRender = <ManageUsers/>;
            break;
        default:
            toRender = null;
            break;
    }

    return(
        <div className='App'>
            <AccountNav changePage={changePage} type={type}/>
            {toRender}
        </div>

    );
}

export default Account;
